body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.TodosApp {
  text-align: center;
}

.btn {
  box-sizing: border-box;
  margin: 0 5px;
  border: none;
  outline: none;
  background: rgb(0, 100, 200);
  color: #fff;
  padding: 10px 20px;;
  cursor: pointer;
  opacity: 0.9;
}

.btn:hover {
  opacity: 1;
}

input, label {
  padding: 10px 0;
  margin: 0 5px;
  box-sizing: border-box;
}

.container {
  width: 60%;
  margin: 0 auto;
}

ul {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  padding: 0;
}

.btn-close {
  margin: 0 5px;
  padding: 2px 4px;
  cursor: pointer;
  color: #f00;
  font-weight: bold;
}

a {
  text-decoration: none;
  margin: 0 2px;
  padding: 2px 5px;
  color: inherit;
  font-weight: 600;
}

a:hover, a:active {
  opacity: 0.5;
}

.nav-active {
  border-bottom: 1px solid black;
}

nav {
  margin-bottom: 10px;
}
